import { ProductDetail } from "@/components/product-detail";

export default async function ProductPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const [product, setProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [quantity, setQuantity] = useState(1);
  const [added, setAdded] = useState(false);

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        setLoading(true);
        const { data } = await axios.get(
          `http://localhost:8000/product/${productId}`
        );
        // If your API returns { product: {...} } adjust:
        setProduct(data.product || data);
      } catch (err: any) {
        setError(
          err.response?.data?.detail || err.message || "Failed to load product"
        );
      } finally {
        setLoading(false);
      }
    };

    fetchProduct();
  }, [productId]);

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        Loading product...
      </div>
    );
  }

  if (error || !product) {
    return (
      <div className="text-center py-12 text-destructive">
        {error || "Product not found"}
      </div>
    );
  }

  const handleAddToCart = async () => {
    if (!product) return;
    try {
      setLoading(true);

      // Call your cart API
      const { data } = await axios.post(
        "http://localhost:8000/cart/items",
        {
          product_id: product.id,
          quantity: quantity,
        },
        { withCredentials: true } // if you use cookie auth
      );

      // Update state to show added
      setAdded(true);

      // Optionally reset quantity back to 1
      setQuantity(1);

      // Optionally show toast / alert
      console.log("Added to cart:", data);
    } catch (err: any) {
      console.error("Failed to add to cart", err);
      alert(
        err.response?.data?.detail || err.message || "Failed to add to cart"
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
      {/* Product Image */}
      <div className="flex items-center justify-center bg-muted rounded-lg overflow-hidden">
        <div className="relative w-full aspect-square">
          <Image
            src={product.image || "/premium-wireless-headphones.png"}
            alt={product.name}
            fill
            className="object-cover"
            priority
          />
        </div>
      </div>

      {/* Product Info */}
      <div className="space-y-6">
        <div>
          <div className="flex items-start justify-between mb-4">
            <div>
              <h1 className="text-3xl font-bold text-foreground mb-2">
                {product.name}
              </h1>
              <p className="text-muted-foreground text-sm">
                {product.category}
              </p>
            </div>
            <Button
              variant="ghost"
              size="icon"
              className="text-muted-foreground hover:text-destructive"
            >
              <Heart className="w-6 h-6" />
            </Button>
          </div>

          <div className="flex items-center gap-3 mb-4">
            <div className="flex items-center gap-1">
              {Array.from({ length: 5 }).map((_, i) => (
                <Star
                  key={i}
                  className={`w-5 h-5 ${
                    i < Math.floor(product.rating)
                      ? "fill-primary text-primary"
                      : "text-muted-foreground"
                  }`}
                />
              ))}
            </div>
            <span className="font-semibold text-foreground">
              {product.rating}
            </span>
            <span className="text-muted-foreground text-sm">
              ({product.reviews} reviews)
            </span>
          </div>
        </div>

        <div className="border-t border-b py-4">
          <div
            className={`text-4xl font-bold text-primary mb-2 ${
              product.stock === 0 ? "line-through" : ""
            }`}
          >
            ${product.price.toFixed(2)}
          </div>
          <p className="text-sm text-muted-foreground">
            {product.stock > 0 ? (
              <span className="text-primary font-medium">
                {product.stock} in stock
              </span>
            ) : (
              <span className="text-destructive font-medium">Out of stock</span>
            )}
          </p>
        </div>

        <p className="text-foreground leading-relaxed">{product.description}</p>

        {/* Quantity Selector */}
        <div className="flex items-center gap-4">
          <span className="text-sm font-medium text-foreground">Quantity:</span>
          <div className="flex items-center border border-border rounded-lg">
            <Button
              onClick={() => setQuantity(Math.max(1, quantity - 1))}
              className="px-4 py-2 text-foreground hover:bg-muted transition-colors"
              variant="ghost"
              disabled={product.stock === 0}
            >
              âˆ’
            </Button>
            <span className="py-1.5 w-10 text-foreground font-medium text-sm text-center flex items-center justify-center">
              {quantity}
            </span>
            <Button
              onClick={() => setQuantity(Math.min(product.stock, quantity + 1))}
              className="px-4 py-2 text-foreground hover:bg-muted transition-colors"
              variant="ghost"
              disabled={quantity >= product.stock || product.stock === 0}
            >
              +
            </Button>
          </div>
        </div>

        {/* Add to Cart Button */}
        <Button
          onClick={handleAddToCart}
          disabled={product.stock === 0}
          className="w-full py-6 text-lg font-semibold"
          size="lg"
        >
          {added ? (
            <>
              <Check className="w-5 h-5 mr-2" />
              Added to Cart
            </>
          ) : (
            <>
              <ShoppingCart className="w-5 h-5 mr-2" />
              Add to Cart
            </>
          )}
        </Button>

        {/* Product Features */}
        <Card className="p-4 bg-muted border-0">
          <h3 className="font-semibold text-foreground mb-3">
            Product Features
          </h3>
          <ul className="space-y-2 text-sm text-muted-foreground">
            <li className="flex items-center gap-2">
              <Check className="w-4 h-4 text-primary" />
              <span>Free shipping on orders over $50</span>
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-4 h-4 text-primary" />
              <span>30-day money-back guarantee</span>
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-4 h-4 text-primary" />
              <span>1-year warranty included</span>
            </li>
          </ul>
        </Card>
      </div>
    </div>
  );
}
